body {
  background: #f1f4f9;
}
a:active,
a:focus,
a:hover {
  background-color: transparent;
}
p,
ul {
  font-size: 16px;
  line-height: 2em;
  color: #5e7184;
}
small {
  font-size: 85%;
}
.content-area {
  min-height: 327px;
}
header,
header .navbar-collapse .nav li a {
  color: #2c3e50;
}
footer .copyright,
header .navbar-collapse .nav {
  text-align: center;
}
header {
  background-color: #f1f4f9;
  position: fixed;
  top: 0;
  width: 100%;
  font-size: 16px;
  letter-spacing: 1px;
  z-index: 99;
  padding: 10px 0;
  transition: top 0.3s ease-in-out;
  -webkit-transition: 0.3s all ease-out;
  -moz-transition: 0.3s all ease-out;
}
header.nav-up {
  top: -80px;
}
header.nav-shadow {
  box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.24);
}
header .navbar-header .logo-name .navbar-brand {
  color: #2c3e50;
  font-weight: 700;
  font-size: 24px;
  letter-spacing: -1px;
}
header .navbar-header .icon-bar {
  background: #2c3e50;
  height: 3px;
}
@media (max-width: 767px) {
  header {
    position: static;
  }
  header .navbar-fixed-top {
    position: fixed;
  }
  header .navbar-collapse {
    background: #fff;
  }
}
header .navbar-collapse .nav li a.active {
  text-decoration: line-through;
  color: #6286c7;
}
header .navbar-collapse .nav li a:hover {
  transition: 0.3s;
  background-color: transparent;
  text-decoration: line-through;
}
.download-resume:focus,
.download-resume:hover {
  text-decoration: none;
  background: #33c681;
  color: #fff;
}
header .navbar-collapse .nav li a:focus {
  background: 0 0;
}
.navbar-fixed-top {
  position: static;
}
.navbar-mobile {
  display: none;
}
@media screen and (max-width: 750px) {
  header {
    height: 50px;
  }
  header .navbar-desktop {
    display: none;
  }
  header .navbar-mobile {
    display: block;
    width: 50%;
    float: left;
  }
  header .navbar-mobile .accordionButton {
    color: #000;
    width: 100%;
    height: 32px;
    float: left;
    cursor: pointer;
    font-size: 14px;
    line-height: 14px;
  }
  header .navbar-mobile .accordionContent {
    width: 100%;
    float: left;
    font-size: 12px;
    line-height: 12px;
    text-align: center;
  }
  header .navbar-mobile .accordionContent a {
    color: rgba(255, 255, 255, 0.623);
  }
}
footer {
  color: #bfacac;
  background: #7fa0c2;
  padding: 2em 0;
  margin-top: 6em;
}
footer .copyright {
  padding-top: 1em;
}
footer .social-networks-ul {
  margin: 15px 0 0;
  text-align: center;
  font-size: 24px;
}
footer .social-networks-ul li {
  padding: 0 0.5em;
}
footer .social-networks-ul li a {
  color: #5e7184;
}
footer .social-networks-ul li a:hover {
  color: #338bc6;
}
@media only screen and (min-width: 992px) {
  footer .copyright {
    padding-top: 0;
    text-align: left;
  }
  footer .social-networks-ul {
    margin: 15px 0 0;
    text-align: right;
  }
  footer .social-networks-ul li {
    padding: 0 0.5em;
  }
}
.jumbotron {
  padding: 4em 0;
}
.jumbotron h1 {
  color: #2c3e50;
  font-size: 3vw;
}
@media (max-width: 767px) {
  .jumbotron h1 {
    font-size: 6vw;
  }
}
.jumbotron h1 span {
  font-size: 5vw;
  font-weight: 700;
}
@media (max-width: 767px) {
  .jumbotron h1 span {
    font-size: 9vw;
  }
}
.jumbotron p {
  margin-bottom: 20px;
}
.jumbotron a.main-cta {
  background: #3866a3;
  padding: 10px 50px;
  color: #fff;
  font-size: 22px;
  border-radius: 40px;
  -webkit-animation-name: Pulse;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
}
.jumbotron a.main-cta:active,
.jumbotron a.main-cta:focus,
.jumbotron a.main-cta:hover {
  text-decoration: none;
}
@keyframes Pulse {
  from,
  to {
    background-color: #29a46a;
    -webkit-box-shadow: 0 0 9px #333;
  }
  50% {
    background-color: #3add91;
    -webkit-box-shadow: 0 0 18px #3add91;
  }
}
.section-heading {
  padding: 1.2em 0;
  background: #2c3e50;
  color: #5e7184;
}
.section-heading h2 {
  margin: 0 0 1em;
  padding: 0;
  font-size: 2em;
  font-weight: 700;
}
.section-heading p {
  padding: 0 0 0 1em;
  margin: 0;
  color: #5e7184;
  font-size: 1.5em;
  font-style: italic;
  border-left: 3px solid #33c681;
}
.card {
  border-radius: 3px;
  background-color: #fff;
  box-shadow: 0 0 6px 0 rgba(30, 45, 62, 0.09);
  transition: box-shadow 0.2s;
  height: 100%;
  min-height: 435px;
  margin: 20px 0;
}
.card:hover {
  box-shadow: 0 10px 15px 0 rgba(30, 45, 62, 0.04),
    0 0 50px 0 rgba(31, 44, 60, 0.05);
}
.card:hover .card-img {
  opacity: 0.9;
}
.card .card-img {
  background-color: #a3afbf;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  height: 230px;
  transition: opacity 0.2s;
  opacity: 0.8;
  border-radius: 3px 3px 0 0;
  box-shadow: 0 10px 15px 0 rgba(30, 45, 62, 0.04);
}
.card .card-content {
  padding: 30px;
}
.card .card-content h4 {
  color: #27303b;
  font-size: 26px;
}
.card .card-tags {
  color: #526173;
  font-size: 12px;
  font-weight: 700;
  display: flex;
  line-height: 22px;
}
.card p {
  color: #526173;
  line-height: 1.5em;
}
.download-resume {
  font-size: 12px;
  background: #3c86b1;
  color: #fff;
  box-shadow: 0 4px #1c2e47;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: inline-block;
  margin: 15px;
  letter-spacing: 1px;
  font-weight: 700;
  outline: 0;
  position: relative;
  width: 17%;
}
.download-resume:hover {
  box-shadow: 0 2px #32689b;
  top: 1px;
}
.download-resume:active {
  box-shadow: 0 0 #3b5d9c;
  top: 5px;
}
@media (max-width: 767px) {
  .download-resume {
    margin: 20px 0;
    width: 100%;
  }
  .prof-img {
    width: 65%;
    margin: 20px auto;
  }
}
.about-me p {
  line-height: 28px;
}
